"use client";

import { useParams, useRouter } from "next/navigation";
import { useState } from "react";
import { motion } from "framer-motion";
import { ArrowLeft, Copy, RefreshCw, CheckCircle2 } from "lucide-react";
import { useCharacter } from "@/context/CharacterContext";
import Link from "next/link";

export default function CharacterResultPage() {
  const params = useParams();
  const router = useRouter();
  const { characters } = useCharacter();
  const [copied, setCopied] = useState<string | null>(null);

  const character = characters.find((char) => char.id === params.id);

  if (!character) {
    return (
      <div className="min-h-screen bg-background flex items-center justify-center px-4">
        <div className="text-center">
          <p className="text-xl font-semibold text-foreground mb-4">Character not found</p>
          <Link
            href="/create"
            className="inline-block px-6 py-3 rounded-xl bg-violet-500 hover:bg-violet-600 text-white font-medium transition-colors"
          >
            Create Character
          </Link>
        </div>
      </div>
    );
  }

  const generated = character.generatedContent || {
    personality: "Personality content will be generated here...",
    backstory: "Backstory content will be generated here...",
    traits: "Core traits will be generated here...",
    speech: "Speech mannerisms will be generated here...",
    initialMessage: "Initial message will be generated here...",
  };

  const copyToClipboard = (text: string, section: string) => {
    navigator.clipboard.writeText(text);
    setCopied(section);
    setTimeout(() => setCopied(null), 2000);
  };

  const sections = [
    {
      id: "personality",
      title: "Personality",
      content: generated.personality,
      editable: true,
    },
    {
      id: "backstory",
      title: "Backstory",
      content: generated.backstory,
      editable: true,
    },
    {
      id: "traits",
      title: "Core Traits",
      content: generated.traits,
      editable: true,
    },
    {
      id: "speech",
      title: "Speech Mannerisms",
      content: generated.speech,
      editable: true,
    },
    {
      id: "initialMessage",
      title: "Initial Message",
      content: generated.initialMessage,
      editable: true,
    },
  ];

  return (
    <div className="min-h-screen bg-background px-4 py-8">
      <div className="max-w-4xl mx-auto">
        {/* Header */}
        <div className="mb-8">
          <Link
            href="/wizard"
            className="inline-flex items-center gap-2 text-muted-foreground hover:text-foreground transition-colors mb-6"
          >
            <ArrowLeft className="w-5 h-5" />
            <span className="text-sm">Back to Wizard</span>
          </Link>
          <h1 className="text-4xl font-bold text-foreground mb-2">{character.basics.name}</h1>
          <p className="text-muted-foreground">
            {character.basics.setting} â€¢ {character.basics.age} years old
          </p>
        </div>

        {/* Character Sections */}
        <div className="space-y-6">
          {sections.map((section, index) => (
            <motion.div
              key={section.id}
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ delay: index * 0.1 }}
              className="glass rounded-2xl p-6 border border-border"
            >
              <div className="flex items-center justify-between mb-4">
                <h2 className="text-xl font-semibold text-foreground">{section.title}</h2>
                <div className="flex items-center gap-2">
                  <button
                    onClick={() => copyToClipboard(section.content, section.id)}
                    className="px-3 py-1.5 rounded-lg glass border border-border hover:bg-white/5 transition-colors flex items-center gap-2 text-sm"
                  >
                    {copied === section.id ? (
                      <>
                        <CheckCircle2 className="w-4 h-4 text-green-400" />
                        Copied!
                      </>
                    ) : (
                      <>
                        <Copy className="w-4 h-4" />
                        Copy
                      </>
                    )}
                  </button>
                  <button
                    className="px-3 py-1.5 rounded-lg glass border border-border hover:bg-white/5 transition-colors flex items-center gap-2 text-sm"
                    title="Rewrite (UI only)"
                  >
                    <RefreshCw className="w-4 h-4" />
                    Rewrite
                  </button>
                </div>
              </div>
              {section.editable ? (
                <textarea
                  value={section.content}
                  onChange={() => { }}
                  className="w-full h-32 px-4 py-3 rounded-xl glass border border-border focus:border-violet-500 transition-all outline-none resize-none text-sm"
                />
              ) : (
                <p className="text-sm text-muted-foreground whitespace-pre-wrap">
                  {section.content}
                </p>
              )}
            </motion.div>
          ))}
        </div>

        {/* Actions */}
        <div className="mt-8 flex gap-4">
          <Link
            href="/create"
            className="flex-1 py-4 rounded-2xl glass border border-border hover:bg-white/5 transition-colors text-center font-medium"
          >
            Create Another
          </Link>
          <button
            onClick={() => router.push("/wizard")}
            className="flex-1 py-4 rounded-2xl bg-violet-500 hover:bg-violet-600 text-white font-semibold transition-colors"
          >
            Edit Character
          </button>
        </div>
      </div>
    </div>
  );
}
